import axios from 'axios';

const HOST = window.host;
const API_KEY = window.apiKey;

const RETURN_CODE = {
  success: 1,
}

const api = axios.create({
  baseURL: `${HOST}/tet2019/`,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
});

api.interceptors.response.use((response) => {
  if (response.data && response.data.returncode === RETURN_CODE.success) {
    return response.data.data;
  } else {
    return Promise.reject(new Error(response.data.returnmessage));
  }
}, (error) => {
  if (error.response) {
    return Promise.reject(new Error(error.response.data.message));
  } else if (error.request) {
    return Promise.reject(new Error('No response was received'));
  } else {
    return Promise.reject(new Error(error.message));
  }
});

export function setAuthorizationHeader(userID, accessToken) {
  api.defaults.headers.userID = userID;
  api.defaults.headers.accessToken = accessToken;
  api.defaults.headers.apiKey = API_KEY;
}

export default api;
