import React, { Component } from 'react'
import { Route, Link, Switch } from 'react-router-dom'
// import Full from './full'
import PageHome from '../containers/page-home'
// import PageGuide from '../containers/page-guide'

function Home() {
  return (
    <div>
      <PageHome/>
      <Link to="/guide">Guide</Link>

      <Link to="/topic">Topic</Link>
    </div>
  )
}

function About() {
  return (
    <div>
      <h2>About</h2>
      <Link to="/">Home</Link>
    </div>
  )
}
function Topics() {
  return (
    <div>
      <h2>Topics</h2>
      <Link to="/">Home</Link>
    </div>
  )
}

class App extends Component {
  render() {
    return (
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/guide" component={About} />
        <Route exact path="/topic" component={Topics} />
      </Switch>
    )
  }
}

// Don't use hight order component for root component when config root router
export default App

